import { Component, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { ToolsProvider } from '../../providers/tools/tools';
import { DomSanitizer } from '@angular/platform-browser';
import { ActivityServiceProvider } from '../../providers/activity-service/activity-service';
import { ReservationServiceProvider } from '../../providers/reservation-service/reservation-service';
import { HotelServiceProvider } from '../../providers/hotel-service/hotel-service';
import { NotificationProvider } from '../../providers/notification/notification';

/**
 * Generated class for the ActivitiesDetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-activities-details',
  templateUrl: 'activities-details.html',
})
export class ActivitiesDetailsPage {
  backgroundImage: any; // to store background image url
  descriptionHtml: any; // to store 'description' translation  
  res: any; //to store return data while language translate
  itemId: any; // to store item id
  item: any = []; // to store full item list
  description: any; // to store description message
  itemArray: any = []; // to store single item from items list
  currencySymbol: any = this.hotelProvider.objHotel.currency.signo; // for currency symbol
  isBookable: boolean = false; // for booking button show and hide

  constructor(public navCtrl: NavController, public navParams: NavParams, public toolsPvdr: ToolsProvider, public sanitized: DomSanitizer, public cdRef: ChangeDetectorRef, private activityProvider: ActivityServiceProvider, private alertCtrl: AlertController, private reservationProvider: ReservationServiceProvider, private hotelProvider: HotelServiceProvider, private notificationProvider: NotificationProvider) {
  }

  ionViewDidLoad() {
    this.itemId = this.navParams.get('itemId'); // get item id 
    this.item = this.activityProvider.objActivity.data; // get all item list
    this.itemArray = this.item.find(x => x.id == this.itemId); // get item from item list with given item id
    this.backgroundImage = this.itemArray.imgPath; // returns image url
    this.description = this.itemArray.descripcionAmpliada; // returns description message
    this.res = this.toolsPvdr.fnLanguageTranslate('view.reservation.label.descriptionPrefix'); // for language translation
    this.res.subscribe((value: string) => {
      this.descriptionHtml = this.sanitized.bypassSecurityTrustHtml(value);
      this.cdRef.detectChanges();
    });
    if(this.reservationProvider.objReservation.noStayGuest){
      this.isBookable = this.itemArray.bookNoStay;
    }else{
      this.isBookable = this.itemArray.esReservable;
    }
    
  }

  fnGotoActivityBooking(item) {
    // check reservation is in a valid date range
    let createdDate = this.reservationProvider.objReservation.createdDate,
      noOfNightsStay = this.reservationProvider.objReservation.nights,
      noStayGuest = this.reservationProvider.objReservation.noStayGuest;
    let dateRange = this.toolsPvdr.getValidReservationDateRange(createdDate, noOfNightsStay, noStayGuest);
    //if date range is not valid then show error message
    if (dateRange == null) {

      let stayOverMsg = this.toolsPvdr.fnLanguageTranslate(['view.reservation.error.dateRange', "common.error", "buttons.ok"]);

      stayOverMsg.subscribe((value: string) => {
        let alertsuccess = this.alertCtrl.create({
          title: value['common.error'],
          message: value['view.reservation.error.dateRange'],
          cssClass: 'cls_alrt',
          buttons: [
            {
              text: value['buttons.ok'],
              role: 'cancel',

            }]
        });
        alertsuccess.present();
        setTimeout(() => { alertsuccess.dismiss(); }, 5000);
      });

    } else {
      //if date range is valid then go to booking page
      this.navCtrl.push("ActivitiesBookingPage", { item: item }, { animate: true, animation: 'transition', duration: 500, direction: 'forward' });
    }
  }

  public fnBack() {
    this.navCtrl.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

}



